import React from 'react';
import { TodoListItem } from './TodoListItem';
import { Todo, ToggleTodo } from './types';

interface Props {
  todos: Todo[];
}

export const TodoList: React.FC<Props> = ({ todos }) => {
  return (
    <ul>
      {todos.map(todo => (
        <TodoListItem key={todo.d_location} todo={todo} />
      ))}
    </ul>
  );
};