import React, { useState } from 'react';
import { AddTodo } from './types';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import { IonCol, IonRow, IonGrid, IonCard, IonPage, IonTitle, IonToolbar, IonToggle, IonButton, IonHeader, IonItem, IonInput, IonLabel } from '@ionic/react';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { InputAdornment } from "@material-ui/core";
import Typography from '@material-ui/core/Typography'
import PhoneIcon from '@material-ui/icons/Phone';
import CommentIcon from '@material-ui/icons/Comment';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandMore from '@material-ui/icons/ExpandMore';
import LocationSearchingIcon from '@material-ui/icons/LocationSearching';
interface Props {
    addTodo: AddTodo;
}

const accordionStyle = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: '80%',
        },
        textfields: {
            width: '100%',
        },
        heading: {
            fontSize: theme.typography.pxToRem(12),
            fontWeight: theme.typography.fontWeightBold,
        },
    }),
);

export const AddTodoForm: React.FC<Props> = ({ addTodo }) => {
    const [d_location, setd_location] = useState('');
    const [d_phone, setd_phone] = useState('');
    const [d_comments, setd_comments] = useState('');
    const accordionclasses = accordionStyle();

    return (

        <Accordion>
            <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
            >
                <Typography className={accordionclasses.heading}>Drop Details</Typography>
            </AccordionSummary>

            <AccordionDetails>
                <IonGrid>
                    <IonRow>
                        <IonCol size="12">
                            <IonItem>
                                <TextField
                                    id="standard-basic"
                                    label="Drop Location"
                                    name="drop_location"
                                    value={d_location}
                                    onChange={e => {
                                        setd_location(e.target.value);
                                    }}
                                    className={accordionclasses.textfields}
                                    InputProps={{
                                        endAdornment: (
                                            <InputAdornment position="end">
                                                <LocationSearchingIcon />
                                            </InputAdornment>
                                        )
                                    }}
                                />                        </IonItem>
                        </IonCol>
                    </IonRow>
                    <IonRow>
                        <IonCol size="12">
                            <IonItem>
                                <TextField
                                    id="standard-basic"
                                    label="Receiver Phone Number"
                                    value={d_phone}
                                    onChange={e => {
                                        setd_phone(e.target.value);
                                    }}
                                    name="drop_pnumber"
                                    className={accordionclasses.textfields}
                                    InputProps={{
                                        endAdornment: (
                                            <InputAdornment position="end">
                                                <PhoneIcon />
                                            </InputAdornment>
                                        )
                                    }}
                                />                          </IonItem>
                        </IonCol>
                    </IonRow>
                    <IonRow>
                        <IonCol size="12">
                            <IonItem>
                                <TextField
                                    id="standard-basic"
                                    label="Comments"
                                    value={d_comments}
                                    onChange={e => {
                                        setd_comments(e.target.value);
                                    }}
                                    name="drop_comments"
                                    className={accordionclasses.textfields}
                                    InputProps={{
                                        endAdornment: (
                                            <InputAdornment position="end">
                                                <CommentIcon />
                                            </InputAdornment>
                                        )
                                    }}
                                />                         </IonItem>
                        </IonCol>
                    </IonRow>
                    <IonRow>
                        <IonCol size="12" >
                            <IonButton expand="block" color="dark" shape="round"
                                type="submit"
                                onClick={e => {
                                    e.preventDefault();
                                    addTodo(d_location, d_phone, d_comments);
                                    setd_location('');
                                    setd_comments('');
                                    setd_phone('');
                                }}
                            >
                                Add Stop
  </IonButton>        </IonCol>
                    </IonRow>
                </IonGrid>

            </AccordionDetails>
        </Accordion>

    );
};



