import { IonRow, IonCol, IonHeader, IonToolbar, IonTitle, IonNote } from '@ionic/react';
import { ReactComponent as BackArrowLogo } from "../../../assets/icons/backarrow.svg";
import React from 'react';
import { useHistory } from 'react-router-dom';
const Navbar: React.FC = () => {
    const history = useHistory();
    const createOrder = () => {
        //history.push('/addgoods');
        history.goBack();
    }
    return (
        <IonRow className="bb-1-grey">
        <IonCol><IonRow className="p-10" onClick={() => createOrder()} ><IonCol size="1.5" className="p-0 mt-5"><BackArrowLogo color="#000000" /></IonCol><IonCol className="m-auto font-18 pl-0"><IonNote color="blue">Back</IonNote></IonCol></IonRow></IonCol>
        <IonCol size="4"></IonCol>
    </IonRow>
    );
};

export default Navbar;
